import React from "react";
import CommonHeader from "../common/common-header";
import CommonCards from "../common/common-cards";
import CardImg from "../../assets/common/card-car.png";

function ServicesComponent() {
  //Get Datas
  const Data = [1, 2, 3, 4, 5, 6];
  return (
    <>
      <CommonHeader />
      <CommonCards
        card_img={CardImg}
        Data={Data}
        common_title={"I want to transport..."}
        card_title={"Car"}
      />
      <CommonCards
        card_img={CardImg}
        Data={Data}
        common_title={"VIP Services"}
        card_title={"Car"}
      />
      <CommonCards
        card_img={CardImg}
        Data={Data}
        common_title={"Other Services"}
        card_title={"Car"}
      />
    </>
  );
}

export default ServicesComponent;
